.HomeProjects {
  background-color: $color-bgoscuro;
}

.HomeProjectsContainer {
  width: 85%;
  margin: 0 auto;
  padding: 50vw 0 8vw;
  &__SectionTitle {
    color: $color-claro;
  }
  &__Title {
    width: 95%;
    margin: 0;
    color: $color-claro;
  }
  &__Line {
    width: 50vw;
    margin: 12vw 0 0vw -7.5vw;
    display: flex;
    border-top: 2px solid $color-secundario;
  }
  &__Description {
    width: 90%;
    margin: 8vw 0 25vw 10%;
    color: $color-claro;
    & span {
      font-weight: 500;
    }
  }
  &__Projects {
    &--Title {
      display: flex;
      width: 100%;
      margin: 0 0 15vw 0;
      color: $color-claro;
    }
  }
  &__MoreButton {
    width: 63vw;
    margin: -2vw auto;
    text-decoration: none;
  }
}

.ProjectBox {
  margin: 0 0 10vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  &__ImageContainer {
    width: 100%;
    &--Image {
      width: 100%;
    }
  }
  &__Title {
    margin: 5vw 0 1vw;
    font-size: 6vw;
    font-weight: 500;
    font-family: $Font1;
    color: $color-claro;
  }
  &__Location {
    margin: 0 0 5vw;
    font-weight: 300;
    font-size: 6vw;
    color: $color-terciario;
    font-family: $Font2;
  }
}

.Project-Logo-Container {
  display: none;
}

.HomeProjectsContainer__MoreButton--Arrow.Button-Arrow {
  width: 22vw;
  height: 22vw;
  background-size: 8vw;
  background-position: 7vw center;
}
.HomeProjectsContainer__MoreButton.Button {
  padding: 5vw 0;
}
.HomeProjectsContainer__MoreButton--Text.Button-Text {
  font-size: 6vw;
}

.HomeProjectsContainer__Description.Mobile {
  display: block;
}

@media screen and (min-width: 1099px) {
  .HomeProjectsContainer {
    width: 75%;
    padding: 10vw 0 6vw;
    position: relative;
    &__Circle {
      width: 5vw;
      height: 5vw;
      top: 18.2vw;
      left: 35vw;
      z-index: 0;
      border-radius: 100vw;
      position: absolute;
      background-color: $color-primario;
    }
    &__Title {
      width: 70%;
      position: relative;
      z-index: 1;
    }
    &__Line {
      width: 40vw;
      margin: 9vw 0 15vw;
      border-top: 2px solid $color-secundario;
    }
    &__Description {
      width: 41%;
      right: 0;
      margin: -18.5vw 0 0;
      position: absolute;
    }
    &__Projects {
      width: 100%;
      margin: 0 auto;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      &--Title {
        margin-bottom: 5vw;
      }
    }
    &__MoreButton {
      width: 14.8vw;
    }
  }

  .ProjectBox {
    width: 45%;
    height: 40vw;
    margin: 0 0 3vw;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    &:nth-child(3) {
      margin-top: 8vw;
    }
    &:nth-child(4) {
      margin-top: -7vw;
    }
    &:nth-child(5) {
      margin-top: 1vw;
    }
    &__ImageContainer {
      width: 100%;
      position: relative;
      &--Image {
        width: 100%;
      }
    }
    &__Title {
      margin: 2vw 0 0.2vw;
      font-size: 1.3vw;
    }
    &__Location {
      margin: 0 0 2vw;
      font-size: 1.3vw;
    }
  }

  .HomeProjectsContainer__Description.Desktop {
    display: block;
  }
  .HomeProjectsContainer__Description.Mobile {
    display: none;
  }

  .Project-Logo-Container {
    width: 100%;
    height: 100%;
    top: 0;
    opacity: 0;
    transition: 0.5s;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    will-change: opacity;
    background-color: rgba(0, 0, 0, 0.3);
  }

  .Project-Logo-Image {
    transition: 0.2s;
    will-change: transform;
    transform: scale(0);
  }
  .Loop-Logo {
    width: 40%;
  }
  .Madero-Logo {
    width: 50%;
  }
  .Daleplast-Logo {
    width: 70%;
  }
  .TuEspacio-Logo {
    width: 45%;
  }

  .ProjectBox:hover .Project-Logo-Container {
    opacity: 1;
  }

  .ProjectBox:hover .Project-Logo-Image {
    transform: scale(1);
  }

  .HomeProjectsContainer__MoreButton--Arrow.Button-Arrow {
    width: 5vw;
    height: 5vw;
    background-size: 1.5vw;
    background-position: 1.7vw center;
  }
  .HomeProjectsContainer__MoreButton.Button {
    padding: 2vw 0;
  }
  .HomeProjectsContainer__MoreButton--Text.Button-Text {
    font-size: 1.5vw;
    letter-spacing: 1px;
  }
}
